.btn-modal {
    padding: 10px 20px;
    display: block;
    margin: 100px auto 0;
    font-size: 18px;
}

.modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}
.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
}

.failed {
    background-color: #f44336; /* red */
    color: white;
}
.success {
    background-color: rgb(23, 91, 23); /* red */
    color: white;
}

.modal-dialog {
    max-width: 800px;
    margin: 2rem auto;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    overflow: hidden;
}

@media (min-width: 576px) {
    .modal-dialog {
        max-width: 900px;
    }
}

@media (min-width: 992px) {
    .modal-dialog {
        max-width: 1200px;
    }
}

.modal-body {
    padding: 2rem;
    font-size: 1.2rem;
    line-height: 1.5;
    overflow-y: auto;
}